@font-face {
  font-family: Droid;
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/droidserifregular.ttf") format('truetype');
}
@font-face {
  font-family: Droid;
  font-weight: 400;
  font-style: italic;
  font-display: swap;
  src: url("../fonts/droidserifitalic.ttf") format('truetype');
}
@font-face {
  font-family: Droid;
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/droidserifbold.ttf") format('truetype');
}
@font-face {
  font-family: Droid;
  font-weight: 700;
  font-style: italic;
  font-display: swap;
  src: url("../fonts/droidserifbolditalic.ttf") format('truetype');
}
@font-face {
  font-family: Ubuntu;
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ubuntulight.ttf") format('truetype');
}
@font-face {
  font-family: Ubuntu;
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ubunturegular.ttf") format('truetype');
}
@font-face {
  font-family: Ubuntu;
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ubuntubold.ttf") format('truetype');
}
img {
  max-width: 100%;
  height: auto;
}
p,
h1,
h2,
h3,
figure {
  margin: 0;
}
ul {
  margin: 0;
  padding: 0;
}
.container {
  padding: 0 15px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1000px) {
  .container {
    padding: 0 45px;
  }
}
.overlay {
  background: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 10;
}
.ui-h2 {
  color: #212A2D;
  font-family: Ubuntu, sans-serif;
  font-size: 30px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 3px;
  line-height: 1;
  text-align: center;
}
.ui-h2:after {
  content: "";
  background: #2DA0E0;
  display: block;
  height: 4px;
  margin: 24px auto;
  width: 30px;
  text-align: center;
}
@media (min-width: 768px) {
  .ui-h2 {
    font-size: 36px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.workshop {
  height: 100%;
  font-family: Droid, sant-serif;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 400;
  color: #212A2D;
}
.workshop__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-width: 320px;
  max-width: 1200px;
  min-height: 100%;
  margin: 0 auto;
}
.workshop-header {
  padding: 12px 0;
  background-color: #2DA0E0;
  min-height: 24px;
  width: 100%;
}
.workshop-header__logo {
  font-family: Droid, sans-serif;
  font-weight: 700;
  line-height: 1.1;
  font-style: italic;
  font-size: 32px;
  margin: 0;
  color: white;
  text-align: center;
  flex: 0 0 265px;
}
.main-nav {
  width: 100%;
}
.main-nav__toogle {
  width: 24px;
  height: 16px;
  justify-self: end;
  align-self: center;
  background-color: transparent;
  border: none;
  cursor: pointer;
  position: relative;
}
.main-nav__toogle::before {
  content: "";
  position: absolute;
  width: 24px;
  border: 1.5px solid #fff;
  background-color: #fff;
  top: 0;
  left: 0;
  box-shadow: 0 8px 0 0 #fff, 0 16px 0 0 #fff;
}
@media (min-width: 768px) {
  .main-nav__toogle {
    display: none;
  }
}
.first-screen {
  background-image: url("../img/bg-start.webp");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 550px;
  position: relative;
}
.first-screen__intro {
  position: absolute;
  z-index: 11;
  top: 25%;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  color: white;
  text-align: center;
}
.first-screen__title {
  font-family: Ubuntu, sans-serif;
  line-height: 1.2;
  font-weight: 700;
  font-size: 24px;
  text-transform: uppercase;
  margin: 0 0 20px 0;
}
.first-screen__text {
  font-family: Droid, sans-serif;
  font-size: 16px;
  font-style: italic;
  line-height: 1.5;
  opacity: 0.7;
  margin: 0 0 24px 0;
}
.first-screen__btn {
  background-color: #2DA0E0;
  text-decoration: none;
  color: #fff;
  font-family: Ubuntu, sans-serif;
  font-size: 12px;
  line-height: 1.5;
  transition: all 0.3s;
  display: inline-block;
  text-transform: uppercase;
  padding: 16px 32px;
  box-sizing: border-box;
  border-radius: 100px;
}
.first-screen__btn:hover,
.first-screen__btn:focus .first-screen__btn:active {
  background-color: #007FB7;
}
.schedule {
  padding: 80px 0;
  box-sizing: border-box;
  width: 100%;
}
.schedule__list {
  list-style: none;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 24px 16px;
}
.schedule__item {
  border: 2px solid #212A2D;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.schedule__item:hover,
.schedule__item:focus {
  border-color: #2DA0E0;
  color: #2DA0E0;
  cursor: default;
  transition: all 0.3s;
}
.about {
  background-color: #EFF7F9;
  padding: 15px 0;
  box-sizing: border-box;
  width: 100%;
}
@media (min-width: 1000px) {
  .about__wrapper {
    display: flex;
  }
}
.about__intro {
  font-style: italic;
  margin: 0 0 48px 0;
}
@media (min-width: 768px) {
  .about__intro {
    font-size: 24px;
    text-align: center;
  }
}
.about__text {
  margin: 0 0 24px 0;
  line-height: 1.5;
}
@media (min-width: 1000px) {
  .about__text-wrapper {
    flex: 0 0 70%;
  }
}
.about__reward {
  text-align: center;
}
.services {
  padding: 80px 0;
  box-sizing: border-box;
}
.services__subtitle {
  text-align: center;
  font-family: Ubuntu, sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.2;
  margin: 16px 0 24px 0;
}
.services__text {
  margin: 0 0 24px 0;
}
@media (min-width: 1000px) {
  .services__item-wrapper {
    display: flex;
    flex-wrap: wrap;
  }
}
.services__item {
  text-align: center;
}
@media (min-width: 1000px) {
  .services__item {
    width: 50%;
  }
}
.gallery {
  background-color: #2DA0E0;
  padding: 80px 0 0 0;
  box-sizing: border-box;
}
.gallery__title {
  color: #fff;
}
.gallery__title::after {
  background: #fff;
}
.gallery__intro {
  color: #fff;
  text-align: center;
  font-style: italic;
  margin: 0 0 48px 0;
}
.gallery__btn {
  background-color: transparent;
  border: 1px solid #c5c5c5;
  color: #fff;
  border-radius: 100px;
  padding: 16px 32px;
  box-sizing: border-box;
  text-transform: uppercase;
  font-size: 12px;
  font-family: Ubuntu, sans-serif;
  cursor: pointer;
}
.gallery__btn:hover {
  background-color: #fff;
  color: #212A2D;
  transition: all 0.3s;
}
.gallery__btn.-current {
  background-color: #fff;
  color: #212A2D;
}
.gallery__btn-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
  margin: 0 0 48px 0;
}
.gallery__example-list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0 -15px;
}
@media (min-width: 768px) {
  .gallery__example-list {
    margin: 0 -30px;
  }
}
@media (min-width: 1000px) {
  .gallery__example-list {
    margin: 0 -45px;
  }
}
.gallery__example-item {
  width: 50%;
  cursor: pointer;
}
@media (min-width: 768px) {
  .gallery__example-item {
    width: 25%;
  }
}
.gallery__example-item.-invisible {
  display: none;
}
.testimonials {
  background-color: #2DA0E0;
}
.contact {
  padding: 80px 0;
  box-sizing: border-box;
}
.contact__wrapper {
  text-align: center;
}
.contact__text {
  margin: 0 0 24px 0;
}
.contact__btn {
  margin: 0 0 24px 0;
}
.contact__social-wrapper {
  display: flex;
  justify-content: center;
  gap: 16px;
}
@media (min-width: 768px) {
  .contact__social-wrapper {
    gap: 32px;
  }
}
.contact__social-item {
  display: block;
  width: 50px;
}
#map {
  height: 400px;
  padding: 0;
  margin: 0 0 24px 0;
}
.workshop-footer {
  background-color: #252F33;
  color: #fff;
  padding: 48px 0;
  box-sizing: border-box;
  font-size: 12px;
  word-spacing: 1px;
  text-align: center;
}
.modal {
  margin: auto;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.4);
  align-items: center;
  display: flex;
}
.modal.-invisible {
  display: none;
}
.modal__content {
  background-color: #fff;
  margin: auto;
  padding: 10px;
  border: 1px solid;
  max-width: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}
.modal__img-wrapper {
  width: 100%;
}
.close {
  width: 16px;
  height: 16px;
  opacity: 0.3;
  cursor: pointer;
  position: relative;
  margin: 0;
}
.close:hover {
  opacity: 1;
}
.close::before,
.close::after {
  content: "";
  position: absolute;
  height: 17px;
  width: 2px;
  background-color: #333;
  top: -5px;
  right: 0;
}
.close::before {
  transform: rotate(45deg);
}
.close::after {
  transform: rotate(-45deg);
}
